.ff-component-scroll-page-table {
  position: relative;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  color: #fff;
  font-size: 14px;
  line-height: 1.5;
  clear: both;
  transition: border 0.3s;
  border: 1px solid #25324d;
  border-radius: 20px;
  height: 100%;
  width: 100%;
}
.ff-component-scroll-page-table table {
  width: 100%;
  border-spacing: 0px;
  color: inherit;
  table-layout: fixed;
}
.ff-component-scroll-page-table th,
.ff-component-scroll-page-table td {
  text-align: center;
  position: relative;
  box-sizing: border-box;
  padding: 16px 8px;
  white-space: normal;
  word-break: break-word;
  transition: box-shadow 0.3s;
}
.ff-component-scroll-page-table tr {
  transition: background-color 0.3s;
}
.ff-component-scroll-page-table tfoot td {
  background: #fff;
}
.ff-component-scroll-page-table-title {
  color: #fff;
  font-size: 20px;
  font-weight: 400;
  position: relative;
  background: url(data:image/svg+xml;charset=utf-8;base64,) no-repeat;
  top: 1px;
  padding: 16px 10;
  border-radius: 4px 4px 0 0;
}
.ff-component-scroll-page-table-footer {
  color: text-color;
  font-size: 20px;
  font-weight: 400;
  position: relative;
  padding: 16px 8px;
  border-radius: 0 0 4px 4px;
}
.ff-component-scroll-page-table-footer::before {
  position: absolute;
  top: -1px;
  left: 0;
  width: 100%;
  height: 1px;
  background: transparent;
  content: "";
}
.ff-component-scroll-page-table-container {
  position: relative;
  border-radius: 4px 4px 0 0;
  padding: 0 20px;
}
.ff-component-scroll-page-table-container-empty {
  line-height: 22px;
  text-align: center;
  margin: 32px 0;
  color: rgba(0, 0, 0, 0.25);
}
.ff-component-scroll-page-table-container-empty > * {
  display: block;
}
.ff-component-scroll-page-table-container-empty img {
  height: 40px;
  margin: 0 auto 8px;
}
.ff-component-scroll-page-table-container-wrapper {
  position: relative;
  overflow: hidden auto;
}
.ff-component-scroll-page-table-container-wrapper::-webkit-scrollbar {
  display: none;
}
.ff-component-scroll-page-table-container-wrapper tr:nth-child(even) {
  background: rgba(199, 235, 255, 0.06);
}
.ff-component-scroll-page-table-container-wrapper tr td {
  font-weight: 400;
  color: #D4E5F7;
}
.ff-component-scroll-page-table-container-wrapper tr:hover {
  background-color: rgba(255, 255, 255, 0.1);
}
.ff-component-scroll-page-table-sticky-header {
  overflow: hidden;
  background-color: transparent;
}
.ff-component-scroll-page-table-sticky-header-table {
  table-layout: fixed;
}
.ff-component-scroll-page-table-sticky-header-table td {
  font-weight: 500;
  background-color: transparent;
  color: #b1e8fc;
}
.ff-component-scroll-page-table-bordered {
  border-radius: 20px;
}
.ff-component-scroll-page-table-bordered .ff-component-title,
.ff-component-scroll-page-table-bordered .ff-component-footer {
  padding: 16px 8px;
}
.ff-component-scroll-page-table-bordered .ff-component-title + .ff-component-container,
.ff-component-scroll-page-table-bordered .ff-component-footer + .ff-component-container {
  border-radius: 0;
}
.ff-component-scroll-page-table-bordered .ff-component-title + .ff-component-container table,
.ff-component-scroll-page-table-bordered .ff-component-footer + .ff-component-container table,
.ff-component-scroll-page-table-bordered .ff-component-title + .ff-component-container .ff-component-thead > tr:first-child > th,
.ff-component-scroll-page-table-bordered .ff-component-footer + .ff-component-container .ff-component-thead > tr:first-child > th {
  border-radius: 0;
}
.ff-component-scroll-page-table-bordered th,
.ff-component-scroll-page-table-bordered td {
  border-top: 0;
  border-left: 0;
}
.ff-component-scroll-page-table-bordered th:last-child,
.ff-component-scroll-page-table-bordered td:last-child {
  border-right: 0;
}
.ff-component-scroll-page-table .ff-component-scroll-page-table-cell-ellipsis {
  max-width: 100%;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.ff-component-scroll-page-table .ff-component-scroll-page-table-cell-align-left {
  text-align: left;
}
.ff-component-scroll-page-table .ff-component-scroll-page-table-cell-align-center {
  text-align: center;
}
.ff-component-scroll-page-table .ff-component-scroll-page-table-cell-align-right {
  text-align: right;
}
.ff-component-scroll-page-table-pagination {
  list-style: none;
  margin: 0;
  text-align: right;
  padding: 16px 0 0;
}
.ff-component-scroll-page-table-pagination-item {
  position: relative;
  border-radius: 4px;
  display: inline-block;
  min-width: 32px;
  height: 32px;
  margin-right: 8px;
  font-family: Arial;
  line-height: 30px;
  text-align: center;
  vertical-align: middle;
  list-style: none;
  border: 1px solid #e8e8e8;
  outline: 0;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  transition: color 0.3s, border-color 0.3s;
  font-size: 12px;
}
.ff-component-scroll-page-table-pagination-item i {
  text-rendering: optimizeLegibility;
  line-height: 0;
  text-transform: none;
  display: inline-block;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
}
.ff-component-scroll-page-table-pagination-item i svg {
  display: inline-block;
  overflow: hidden;
  line-height: 1;
}
.ff-component-scroll-page-table-pagination-item:not(.ff-component-pagination-disable):hover,
.ff-component-scroll-page-table-pagination-item-active {
  color: #fff;
  border-color: #fff;
}
.ff-component-scroll-page-table-pagination-item:last-child {
  margin-right: 0;
}
.ff-component-scroll-page-table-pagination-disable {
  cursor: not-allowed;
  border-color: #d9d9d9;
  color: rgba(0, 0, 0, 0.25);
}
